}

@media screen and (max-width: 576px) {
  .wrapper {
    padding-left: 0px;
    padding-right: 0px;
  }

  body {
    font-size: 14px;
  }

  .logo {
    padding-bottom: 25px;
  }

  .header_left {
    width: calc(83.33333333333334% - 15px);
