  }

  .first_screen_text p {
    font-size: 30px;
  }

  .first_screen_text p {
    padding-top: 55px;
  }

  .margin_bottom4 {
    margin-bottom: 0px;
  }

  .third-of-block {
    width: calc(100% - 15px);
  }

  .third-of-block {
    margin-top: 15px;
  }

  .two-of-block {
    width: calc(100% - 15px);
  }

  .two-of-block {
    margin-top: 15px;
  }

  .mobile-none {
